export default "/* ****************************** COMMON EFFECTS ****************************** */\r\n/* Parallax effect */\r\n.parallax {background-attachment:fixed;background-position:center;background-repeat:no-repeat;background-size:cover}\r\n.photoCred {background-color:black;color:white;text-decoration:none;padding:4px 6px;font-size:12px;font-weight:bold;line-height:1.2;display:inline-block;border-radius:3px}\r\nh1 {font-family:Helvetica,sans-serif;text-align:center;line-height:50px}\r\nh2, h3 {font-family:Helvetica,sans-serif;text-align:center;line-height:50px}\r\nh4 {font-family:Helvetica,sans-serif;text-align:center;font-size:17pt;color:rgb(100, 100, 100);line-height:30px}\r\nh5 {font-family:Helvetica,sans-serif;text-align:center;margin-top:-20px}\r\nh6 {font-family:Helvetica,sans-serif;text-align:center;letter-spacing:2px}\r\n/* ****************************** GRID COLUMN LAYOUTS ****************************** */\r\n.rows { display: flex; flex-wrap: wrap; padding: 0 4px; }\r\n.column-3 { flex: 33%; max-width: 33%; padding: 0 4px; }\r\n.column-3 img { margin-top: 8px; vertical-align: middle; width: 100%; }\r\n@media screen and (max-width: 1200px) {.column-3 { flex: 100%; max-width: 100%; }}\r\n/* ****************************** BLOG COLUMN LAYOUTS ****************************** */\r\n/* Columns on the front page */\r\n.leftcolumn {float:left;width:75%;}\r\n.rightcolumn {float:left;width:25%;padding-left:20px;}\r\n@media screen and (max-width: 1000px) {.leftcolumn, .rightcolumn {width: 100%;padding: 0;} }\r\n.blog-layout:after {content:\"\";display: table;clear: both;}\r\n@media screen and (min-width: 1001px) {\r\n  #sticky-right-col {\r\n    position: -webkit-sticky;\r\n    position: sticky;\r\n    top: 0;\r\n    z-index: -1;\r\n    padding: 60px 10px;\r\n    font-size: 20px;\r\n  } \r\n}\r\n/* ****************************** CARD EFFECTS ****************************** */\r\n.card {background-color: rgb(250, 250, 250);box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);}\r\n.card p, .card h3 {padding:20px;}\r\n/* Card within a card */\r\n.card-container { padding: 2px 16px; }\r\n.mini-card { box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2); transition: 0.3s; width: 100%; margin-bottom: 20px; text-align: justify; }\r\n.mini-card:hover { box-shadow: 0 8px 16px 0 rgba(0,0,0,0.2); }\r\n.mini-card-container { padding: 2px 16px; }\r\n/* ****************************** TAGS ****************************** */\r\n.tags {margin-bottom:20px}\r\n.tags p {font-size:10pt;background-color:lightgrey;color:rgb(97, 97, 97);padding:2px 10px;border-radius:20px;margin-bottom:-5px;}\r\n.tag {margin:10px 5px;display:inline-block;}\r\n/* ****************************** IMAGES AND GIFS ****************************** */\r\n.blog-image {width:50%;height:50%}\r\n@media screen and (max-width: 900px) {.blog-image {width:100%;height:100%}}\r\n.content-photo-credit p {font-size:10pt;text-align:center;color:grey}\r\n#gif-image {width:360px;height:339px}\r\n@media screen and (max-width: 900px) {.gif-image {width:288px;height:271px}}\r\n/* ****************************** WRITTEN CONTENT ****************************** */\r\n.subheading {font-size:22pt;text-align:left;font-style:bold;line-height:40px}\r\n.subsubheading {font-size:19pt;text-align:left;font-style:bold;line-height:40px;color:rgb(80, 80, 80)}\r\n.sidebar-bottom-headings {font-family:Helvetica,sans-serif;text-align:center;line-height:50px;letter-spacing:3px;color:rgb(100, 100, 100)}\r\n.written-content-container { text-align: center; }\r\n.written-content { width: 70%; display: inline-block; text-align: justify; color: rgb(54, 54, 54); }\r\n@media screen and (max-width: 920px) {.written-content { width: 90%; }}\r\n.blog-date {color: rgb(136, 136, 136)}\r\n.fadedText {color:rgb(100, 100, 100)}\r\np, ul, ol, blockquote {font-family:Helvetica,sans-serif;font-size:16pt;line-height:30px}\r\np, blockquote {text-align:justify}\r\nul, ol {text-align:left}\r\ntable {font-size:15pt;line-height:20px}\r\ntd {padding:10px}\r\ntr:nth-child(even) {background-color:whitesmoke;}\r\nth {padding:10px}\r\n/*# sourceMappingURL=data:application/json;base64, */"