export default "/* ****************************** COMMON EFFECTS ****************************** */\r\n/* Parallax effect */\r\n.parallax {background-attachment:fixed;background-position:center;background-repeat:no-repeat;background-size:cover}\r\n.photoCred {background-color:black;color:white;text-decoration:none;padding:4px 6px;font-size:12px;font-weight:bold;line-height:1.2;display:inline-block;border-radius:3px}\r\nh1 {font-family:Helvetica,sans-serif;text-align:center;line-height:50px}\r\nh2, h3 {font-family:Helvetica,sans-serif;text-align:center;line-height:50px}\r\nh4 {font-family:Helvetica,sans-serif;text-align:center;font-size:14pt;color:rgb(100, 100, 100);line-height:30px}\r\nh5 {font-family:Helvetica,sans-serif;text-align:center;margin-top:-20px}\r\n/* ****************************** BLOG COLUMN LAYOUTS ****************************** */\r\n/* Columns on the front page */\r\n.leftcolumn {float:left;width:75%;}\r\n.rightcolumn {float:left;width:25%;padding-left:20px;}\r\n@media screen and (max-width: 1000px) {.leftcolumn, .rightcolumn {width: 100%;padding: 0;} }\r\n.blog-layout:after {content:\"\";display: table;clear: both;}\r\n@media screen and (min-width: 1001px) {\r\n  #sticky-right-col {\r\n    position: -webkit-sticky;\r\n    position: sticky;\r\n    top: 0;\r\n    z-index: -1;\r\n    padding: 60px 10px;\r\n    font-size: 20px;\r\n  } \r\n}\r\n/* ****************************** CATAGORIES ****************************** */\r\n.category-card {background-color:white;box-shadow: 0 4px 8px 0 lightgrey}\r\n.categories-list {color: lightgrey; margin-top: 20px; background-color: #404040; padding: 0;border-radius:10px }\r\n.card-blog-category {text-decoration:none;color:darkgrey;margin-bottom: 15px; background-color:rgb(241, 241, 241); box-shadow: 0 2px 4px 0 rgba(0,0,0,0.2); transition: 0.3s; padding: 0px;}\r\n.card-blog-category h4 {margin: -5px;text-decoration:none;color:rgb(160, 160, 160)}\r\n@media screen and (max-width: 1200px) {.card-blog-category { margin: 10px; }}\r\n.card-blog-category:hover {background-color: #4e4e4e;text-decoration:none;}\r\n#active-blog-category {background-color: #272727;}\r\n/* ****************************** CARD EFFECTS ****************************** */\r\n.card {background-color: rgb(250, 250, 250);box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);}\r\n.card p, .card h3 {padding:20px;}\r\n/* Card within a card */\r\n.card-container { padding: 2px 16px; }\r\n/* ****************************** SEARCH BAR ****************************** */\r\n.search-container {text-align: center }\r\n#myInput {background-repeat: no-repeat;width: 80%;text-align:center;font-size: 16px;padding: 12px 20px 12px 40px;border: 1px solid #ddd;margin-bottom: 12px;}\r\n@media screen and (max-width: 1200px) {#myInput { width: 90%; }}\r\n#myUL {text-align:center;list-style-type: none;padding: 0;margin: 0;}\r\n#myUL li {text-align: center;}\r\n/* ****************************** BLOG ARTICLE PREVIEWS ****************************** */\r\n.blog-item {padding:0px;text-align: center;border: 1px solid #ddd;margin-top: -1px;margin: 10px 0px;text-decoration: none;font-size: 18px;color: rgb(85, 85, 85);display: block;border:none}\r\n.blog-item h2 {padding:10px}\r\n.article-preview-image {width: 100%;padding: 20px;}\r\n@media screen and (max-width: 1500px) {.blog-item { margin: 5px 5px;}}\r\n.date {font-size: 14px; color: rgb(133, 133, 133)}\r\n.blog-description {font-size: 14px; color: rgb(85, 85, 85); padding: 20px 200px;text-align:center}\r\n@media screen and (max-width: 900px) {.blog-description { padding: 20px;}}\r\n.blog-post-preview {width:100%;height:100%}\r\n@media screen and (max-width: 900px) {.blog-post-preview {width:100%;height:100%}}\r\n.fadedText {color:rgb(100, 100, 100)}\r\n.sidebar-bottom-headings {font-family:Helvetica,sans-serif;text-align:center;line-height:50px;letter-spacing:3px;color:rgb(100, 100, 100)}\r\n/* ******************************* LINKS ******************************* */\r\na {text-decoration: none;}\r\na:hover {text-decoration: none;}\r\n\r\n/*# sourceMappingURL=data:application/json;base64, */"