on:hover { background-color: rgb(21, 32, 43); text-decoration: none; color: white;}\r\n.read-more-button:active { text-decoration: none; color: white; box-shadow: 0 5px rgb(180, 180, 180); transform: translateY(4px); }\r\n.read-more-button:visited { color: white; text-decoration: none }\r\n/* Specific styling for download button */\r\n.download-button { background-color: darkred}\r\n.download-button:hover { background-color: rgb(87, 0, 0); text-decoration: none; color: white;}\r\n.download-button:active { text-decoration: none; color: white; box-shadow: 0 5px rgb(180, 180, 180); transform: translateY(4px); }\r\n.download-button:visited { color: white; text-decoration: none }\r\n/*# sourceMappingURL=data:application/json;base64, */"